import {EventManager} from "./EventManager.mjs";
class Point2D {
    constructor(x, y) {
        this.x = x;
        this.y = y;
    }
}
class AbstractShape {
    constructor() {
        this.id = AbstractShape.counter++;
    }
    getBackgroundColor() {
        return this.backgroundColor;
    }
    setBackgroundColor(backgroundColor) {
        this.backgroundColor = backgroundColor;
    }
    getBorderColor() {
        return this.borderColor;
    }
    setBorderColor(borderColor) {
        this.borderColor = borderColor;
    }
    handleColour(ctx) {
        if (this.getBackgroundColor()) {
            ctx.fillStyle = this.getBackgroundColor();
            ctx.fill();
        }
        if (this.getBorderColor()) {
            ctx.strokeStyle = this.getBorderColor();
        }
    }
}
AbstractShape.counter = 0;
class AbstractFactory {
    constructor(shapeManager) {
        this.shapeManager = shapeManager;
    }
    handleMouseDown(x, y) {
        this.from = new Point2D(x, y);
    }
    handleMouseUp(x, y) {
        // remove the temp line, if there was one

        let eventManager = new EventManager(this.shapeManager);

        //let tmpShapeEvent = eventManager.setEvent(this.tmpShape);

        //let newShapeEvent = eventManager.getEvent(this.createShape(this.from, new Point2D(x, y)));

        if (this.tmpShape) {
            eventManager.createEvent('removeShape', this.tmpShape);
        }
        console.log("type of " + typeof newShape);
        this.tmpShape = this.createShape(this.from, new Point2D(x, y))
        eventManager.createEvent('addShape', this.tmpShape);
        this.from = undefined;
        //this.shapeManager.addShape(this.createShape(this.from, new Point2D(x, y)));
        //this.from = undefined;


    }
    handleMouseMove(x, y) {
        // show temp circle only, if the start point is defined;
        let eventManager = new EventManager(this.shapeManager);
        if (!this.from) {
            return;
        }
        if (!this.tmpTo || (this.tmpTo.x !== x || this.tmpTo.y !== y)) {
            this.tmpTo = new Point2D(x, y);
            if (this.tmpShape) {
                // remove the old temp line, if there was one
                //this.shapeManager.removeShapeWithId(this.tmpShape.id, false);
                eventManager.createEvent('removeShape', this.tmpShape);
            }
            // adds a new temp line
            this.tmpShape = this.createShape(this.from, new Point2D(x, y));
            eventManager.createEvent('addShape', this.tmpShape);

            //this.shapeManager.addShape(this.tmpShape);
        }
    }
}
export class Line extends AbstractShape {
    constructor(from, to) {
        super();
        this.from = from;
        this.to = to;
    }
    draw(ctx, selected) {
        ctx.beginPath();
        ctx.moveTo(this.from.x, this.from.y);
        ctx.lineTo(this.to.x, this.to.y);
        super.handleColour(ctx);
        ctx.stroke();
        if (selected) {
            let factor = 4;
            let width = 10;
            let height = 10;
            let fromX = this.from.x;
            let fromY = this.from.y;
            let toX = this.to.x;
            let toY = this.to.y;
            //start point
            ctx.beginPath();
            ctx.rect(fromX - factor, fromY - factor, width, height);
            ctx.stroke();
            //endpoint
            ctx.beginPath();
            ctx.rect(toX - factor, toY - factor, width, height);
            ctx.stroke();
        }
    }
}
export class LineFactory extends AbstractFactory {
    constructor(shapeManager) {
        super(shapeManager);
        this.label = "Linie";
    }
    createShape(from, to) {
        return new Line(from, to);
    }
}
export class Circle extends AbstractShape {
    constructor(center, radius) {
        super();
        this.center = center;
        this.radius = radius;
    }
    draw(ctx, selected) {
        ctx.beginPath();
        ctx.arc(this.center.x, this.center.y, this.radius, 0, 2 * Math.PI);
        super.handleColour(ctx);
        ctx.stroke();
        if (selected) {
            let factor = 4;
            let width = 10;
            let height = 10;
            let centerX = this.center.x;
            let centerY = this.center.y;
            let radius = this.radius;
            //bottom
            ctx.beginPath();
            ctx.rect(centerX - factor, centerY + radius - factor, width, height);
            ctx.stroke();
            //top
            ctx.beginPath();
            ctx.rect(centerX - factor, centerY - radius - factor, width, height);
            ctx.stroke();
            //left
            ctx.beginPath();
            ctx.rect(centerX - radius - factor, centerY - factor, width, height);
            ctx.stroke();
            //right
            ctx.beginPath();
            ctx.rect(centerX + radius - factor, centerY - factor, width, height);
            ctx.stroke();
        }
    }
}
export class CircleFactory extends AbstractFactory {
    constructor(shapeManager) {
        super(shapeManager);
        this.label = "Kreis";
    }
    createShape(from, to) {
        return new Circle(from, CircleFactory.computeRadius(from, to.x, to.y));
    }
    static computeRadius(from, x, y) {
        const xDiff = (from.x - x),
            yDiff = (from.y - y);
        return Math.sqrt(xDiff * xDiff + yDiff * yDiff);
    }
}
export class Rectangle extends AbstractShape {
    constructor(from, to) {
        super();
        this.from = new Point2D(from.x, from.y);
        this.to = new Point2D(to.x, to.y);
    }
    draw(ctx, selected, backgroundColor, borderColor) {
        ctx.beginPath();
        ctx.rect(this.from.x, this.from.y, this.to.x - this.from.x, this.to.y - this.from.y);
        super.handleColour(ctx);
        ctx.stroke();
        if (selected) {
            let factor = 4;
            let width = 10;
            let height = 10;
            let fromX = this.from.x;
            let fromY = this.from.y;
            let toX = this.to.x;
            let toY = this.to.y;
            //left top
            ctx.beginPath();
            ctx.rect(fromX - factor, fromY - factor, width, height);
            ctx.stroke();
            //left bottom
            ctx.beginPath();
            ctx.rect(fromX - factor, toY - factor, width, height);
            ctx.stroke();
            //right top
            ctx.beginPath();
            ctx.rect(toX - factor, fromY - factor, width, height);
            ctx.stroke();
            //right bottom
            ctx.beginPath();
            ctx.rect(toX - factor, toY - factor, width, height);
            ctx.stroke();
        }
    }
}
export class RectangleFactory extends AbstractFactory {
    constructor(shapeManager) {
        super(shapeManager);
        this.label = "Rechteck";
    }
    createShape(from, to) {
        return new Rectangle(from, to);
    }
}
export class Triangle extends AbstractShape {
    constructor(p1, p2, p3) {
        super();
        this.p1 = p1;
        this.p2 = p2;
        this.p3 = p3;
    }
    draw(ctx, selected, backgroundColor, borderColor) {
        ctx.beginPath();
        ctx.moveTo(this.p1.x, this.p1.y);
        ctx.lineTo(this.p2.x, this.p2.y);
        ctx.lineTo(this.p3.x, this.p3.y);
        ctx.lineTo(this.p1.x, this.p1.y);
        super.handleColour(ctx);
        ctx.stroke();
        if (selected) {
            let factor = 4;
            let width = 10;
            let height = 10;
            let p1X = this.p1.x;
            let p1Y = this.p1.y;
            let p2X = this.p2.x;
            let p2Y = this.p2.y;
            let p3X = this.p3.x;
            let p3Y = this.p3.y;
            ctx.beginPath();
            ctx.rect(p1X - factor, p1Y - factor, width, height);
            ctx.stroke();
            ctx.beginPath();
            ctx.rect(p2X - factor, p2Y - factor, width, height);
            ctx.stroke();
            ctx.beginPath();
            ctx.rect(p3X - factor, p3Y - factor, width, height);
            ctx.stroke();
        }
    }
}
export class TriangleFactory {
    constructor(shapeManager) {
        this.shapeManager = shapeManager;
        this.label = "Dreieck";
    }
    handleMouseDown(x, y) {
        let eventManager = new EventManager(this.shapeManager);
        if (this.tmpShape) {
            //this.shapeManager.removeShapeWithId(this.tmpShape.id, false);
            //this.shapeManager.addShape(new Triangle(this.from, this.tmpTo, new Point2D(x, y)));
            eventManager.createEvent('removeShape', this.tmpShape);
            eventManager.createEvent('addShape', new Triangle(this.from, this.tmpTo, new Point2D(x, y)));
            this.from = undefined;
            this.tmpTo = undefined;
            this.tmpLine = undefined;
            this.thirdPoint = undefined;
            this.tmpShape = undefined;
        } else {
            this.from = new Point2D(x, y);
        }
    }
    handleMouseUp(x, y) {
        let eventManager = new EventManager(this.shapeManager);
        // remove the temp line, if there was one
        if (this.tmpLine) {
            /*this.shapeManager.removeShapeWithId(this.tmpLine.id, false);
            this.tmpLine = undefined;
            this.tmpTo = new Point2D(x, y);
            this.thirdPoint = new Point2D(x, y);
            this.tmpShape = new Triangle(this.from, this.tmpTo, this.thirdPoint);
            this.shapeManager.addShape(this.tmpShape);*/
            eventManager.createEvent('removeShape', this.tmpLine);
            this.tmpTo = new Point2D(x, y);
            this.thirdPoint = new Point2D(x, y);
            this.tmpShape = new Triangle(this.from, this.tmpTo, this.thirdPoint);

            eventManager.createEvent('addShape', this.tmpShape);
        }
    }
    handleMouseMove(x, y) {
        let eventManager = new EventManager(this.shapeManager);
        // show temp circle only, if the start point is defined;
        if (!this.from) {
            return;
        }
        if (this.tmpShape) { // second point already defined, update temp triangle
            if (!this.thirdPoint || (this.thirdPoint.x !== x || this.thirdPoint.y !== y)) {
                this.thirdPoint = new Point2D(x, y);
                /*if (this.tmpShape) {
                    // remove the old temp line, if there was one
                    this.shapeManager.removeShapeWithId(this.tmpShape.id, false);
                }
                // adds a new temp triangle
                this.tmpShape = new Triangle(this.from, this.tmpTo, this.thirdPoint);
                this.shapeManager.addShape(this.tmpShape);*/

                if (this.tmpShape) {
                    // remove the old temp line, if there was one
                    eventManager.createEvent('removeShape', this.tmpShape);
                }
                // adds a new temp line
                this.tmpShape = new Triangle(this.from, this.tmpTo, this.thirdPoint);
                eventManager.createEvent('addShape', this.tmpShape);
            }
        } else { // no second point fixed, update tmp line
            if (!this.tmpTo || (this.tmpTo.x !== x || this.tmpTo.y !== y)) {
                this.tmpTo = new Point2D(x, y);

                if (this.tmpLine) {
                    // remove the old temp line, if there was one
                    eventManager.createEvent('removeShape', this.tmpLine);
                }
                // adds a new temp line
                this.tmpLine = new Line(this.from, this.tmpTo);
               eventManager.createEvent('addShape', this.tmpLine);
            }
        }
    }
}

export function createShapeFromEventData(shape) {
    const type = shape.type;

    if (type === 'Line') {
        //const from = new Point2D(shape.data.from.x, shape.data.from.y);
        //const to = new Point2D(shape.data.to.x, shape.data.to.y);
        //return new Line(from, to);
        return new Line(shape.data.from, shape.data.to);
    } else if (type === 'Circle') {
        //const center = new Point2D(shape.data.center.x, shape.data.center.y);
        //const radius = shape.data.radius;
        //return new Circle(center, radius);
        return new Circle(shape.data.center, shape.data.radius);
    } else if (type === 'Rectangle') {
        //const from = new Point2D(shape.data.from.x, shape.data.from.y);
        //const to = new Point2D(shape.data.to.x, shape.data.to.y);
        //return new Rectangle(from, to);
        return new Rectangle(shape.data.from, shape.data.to);
    } else if (type === 'Triangle') {
        /*const p1 = new Point2D(shape.data.p1.x, shape.data.p1.y);
        const p2 = new Point2D(shape.data.p2.x, shape.data.p2.y);
        const p3 = new Point2D(shape.data.p3.x, shape.data.p3.y);
        return new Triangle(p1, p2, p3);*/
        return new Triangle(shape.data.p1, shape.data.p2, shape.data.p3);
    }

    return null;
}

