export class Menu {
    #items = null;
    #htmlElement = null;
    #hidden = true;

    constructor() {
        this.#items = [];
        this.className = "Menu";
    }

    addItem(item) {
        console.log('Add item');
        if (!this.#items.includes(item)) {
            this.#items.push(item);
        }
    }

    addItemAt(item, index) {
        console.log("add item at an index");
        if (!this.#items.includes(item)) {
            this.#items.splice(index, 0, item);
        }
    }

    addItems(...args) {
        args.forEach((arg) => this.addItem(arg));
    }

    removeItem(item) {
        console.log('Removes item');
        if (this.#items.includes(item) && !this.#hidden) {
            this.#items.splice(this.#items.indexOf(item), 1);
        }
    }

    show(x, y) {
        console.log('show menu');
       if (this.#hidden) { 
            document.addEventListener('click', this.#handleClick, true);
            let popupmenu = document.createElement("div");
            popupmenu.className = "popupmenu";
            popupmenu.style.left = `${x}px`; 
            popupmenu.style.top = `${y}px`;

            this.#items.forEach((item) => {
                console.log("#items");
                let itemElement = item.render();
                itemElement.addEventListener("click", () => item.getCallback(this))
                popupmenu.appendChild(itemElement);
            });
            
            document.body.appendChild(popupmenu);
        
            this.#hidden = false;
            this.#htmlElement = popupmenu;
        }
    }

    hide() {
        console.log('hide menu');
        if (!this.#hidden) {
            document.removeEventListener('click', this.#handleClick, true);
            this.#htmlElement.remove();
            this.#hidden = true;
            this.#htmlElement = null;
        }
    }

    #handleClick = (event) => {
        let className = event.target.className;
        const isClickOutside = className !== 'popupmenu-item' &&
            className !== 'popupmenu'
            && className !== 'popupmenu-item-separator'
            && className !== 'lblRadio'
            && className !== 'color-radioBttn'
            && className !== 'background-color-list';


        if (isClickOutside){
             event.stopImmediatePropagation();
             this.hide();
         }

        console.log("type: "  + event.type + " className " + className);
    }
}
